body {
  height: 500vh;
  font-family: sans-serif;
}

.counter {
  justify-content: center;
  align-items: center;
  display: flex;
  position: sticky;
  top: 100px;
}

.counter-resize {
  top: 200px;
}

.box {
  border: 3px solid #f4c003;
  border-radius: 4px;
  margin-left: 8px;
  margin-right: 8px;
  padding: 12px;
}

.input {
  margin: 30px;
  display: inline-block;
}

form {
  width: 200px;
  text-align: center;
  margin-left: 50px;
}

legend {
  font-size: 30px;
}

fieldset {
  background-color: #00000024;
  border-color: #f4c003;
  border-radius: 8px;
}

label {
  margin: 10px;
  display: block;
}

input {
  border-color: #f4c003;
  border-radius: 8px;
  outline: none;
  margin: 10px auto 0;
  padding: 5px;
  display: block;
}

.wrong {
  border: 4px solid red;
}

.correct {
  border: 4px solid green;
}

.visual-hidden {
  display: none;
}

